import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import { GroupService } from "../../services/group.service";
import { UsersService } from "../../services/users.service";
import { Observable } from 'rxjs/Observable';
import { User } from '../../models/user';
import { Comment } from '../../models/comment';
import {Clase} from "../../models/clase";


@Component({
  selector: 'app-group-edit-page',
  templateUrl: './group-edit-page.component.html',
  styleUrls: ['./group-edit-page.component.css'],
  
})
export class GroupEditPageComponent implements OnInit {
  comments: Comment[]=[];
  co:Comment;
  co2:Comment;
  groupForm: FormGroup; 
  user :User;
  clases: Clase[]=[];

  constructor(
    public groupService: GroupService,
    public usersService: UsersService,
    public formBuilder: FormBuilder,
    public router: Router,
  ) {
    
  }

  addClass(){
    this.clases.push(new Clase(Number(sessionStorage.getItem("groupId")), String(this.groupForm.get('date').value), this.groupForm.get('hour').value , this.groupForm.get('place').value, 0, this.groupForm.get('name').value,0,sessionStorage.getItem('username')));
  }

  ngOnInit() {

    this.groupForm = this.formBuilder.group({
      name: '',
      place: '',
      description:'',
      category: '',
      image:'',hour:'',date:''
    });
    this.usersService.findUserByUsername(sessionStorage.getItem('username')).subscribe(usersResponse4 => {
      this.user = usersResponse4;
    })

  }

  onSubmit() {
    sessionStorage.setItem('groupname', this.groupForm.get('name').value);
    this.groupService.create1(0,
      this.groupForm.get('name').value,
      this.user.username,this.comments,this.groupForm.get('description').value,this.groupForm.get('category').value,
      0,0,this.groupForm.get('image').value,this.clases
    ).subscribe(serverResponse=>{
        sessionStorage.setItem('groupId', String(serverResponse.id));
        this.router.navigate(['/group']);
    }, error=>{
      console.log('ERROR');
    });

 
  }

}


