import { Component, OnInit } from '@angular/core';
import { GroupService } from '../../services/group.service';
import { Router } from '@angular/router';
import { Group } from '../../models/group'

@Component({
  selector: 'app-group-list',
  templateUrl: './group-list.component.html',
  styleUrls: ['./group-list.component.css']
})
export class GroupListComponent implements OnInit {
  groups:Group[]=[];
  name:string;
  constructor( public groupService:GroupService,
               public router: Router ) { }

  ngOnInit() {
    this.name=sessionStorage.getItem('category');
    this.groupService.getGroupByCategory(this.name).subscribe(categories =>{
      this.groups=categories;
    })
  }

    back() {
        this.router.navigate(['/welcome']);
    }

  entergr(name:string,id:number) {
    console.log(name)
    sessionStorage.setItem('groupname',name);
      sessionStorage.setItem('groupId', String(id));
      this.router.navigate(['/group']);
  }
 

}



