export class Comment {
    contenido: string;
    groupId: number;
    usuario: string;
    fecha: string;
    id: number;




    constructor(contenido: string, groupId: number, usuario: string, fecha: string, id: number) {
        this.contenido = contenido;
        this.groupId = groupId;
        this.usuario = usuario;
        this.fecha = fecha;
        this.id = id;
    }

}





