import { Component } from '@angular/core';
import { AuthService } from './common/auth.service';
import { Router } from '@angular/router';
import { Group } from './models/group';
import { GroupService } from "./services/group.service";
import { FormGroup, FormBuilder, FormControl } from '@angular/forms';
import { UsersService } from './services/users.service';
import { User } from './models/user';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css',]
})
export class AppComponent {
  title = 'app';
  signInForm: FormGroup;
  loginError: String;
  user:User;
  group: Group[] = [];
  constructor(
    public authService: AuthService,
    public router: Router,
    public groupService: GroupService,
    public usersService: UsersService,
    public formBuilder: FormBuilder
  ) {
    if (!this.authService.isLoggedIn()) {
      this.router.navigate(['/']);
    }
  }

  ngOnInit() {
    this.signInForm = this.formBuilder.group({
      username: '',
      password: '',
    });
  }
  longitud() {
    if (this.group.length === 0) {
      return false;
    }
    else {
      return true;
    }
  }

  isLoggedIn() {
    return this.authService.isLoggedIn();
  }

  signOut() {
    this.authService.signOut();
  }
  entergr(name: string, id: number) {
    sessionStorage.setItem('groupname', name);
    sessionStorage.setItem('groupId', String(id));
      this.router.navigateByUrl('/DummyComponent', true);
      this.router.navigate(['/group']);
  }

  onSubmit(search) {
    this.groupService.getGroupByName(search).subscribe(response => {
      this.group = response;
    });
  }


  doLogin() {
    sessionStorage.setItem('username', this.signInForm.get('username').value);
    this.usersService.login(
      this.signInForm.get('username').value,
      this.signInForm.get('password').value).subscribe(loginResponse => {
        this.router.navigate(['/welcome']);
      }, error => {
        this.loginError = 'Error Signing in: ' + (error && error.message ? error.message : '');
      })
      this.usersService.findUserByUsername(this.signInForm.get('username').value).subscribe(usersResponse5 => {
          this.user = usersResponse5;
      })
  }


}



