import { Group } from "./group";
import {Clase} from "./clase";


export class User {
    nombre: string;
    lastname: string;
    image: string;
    phone: string;
    password: string;
    email: string;
    description: string;
    type: string;
    username: string;
    rate: number;
    totalVotes: number;
    clases: Clase[];
    cupo: number;

    constructor(nombre: string, lastname: string, image: string, phone: string, password: string, email: string, description: string, type: string, username: string, rate: number, totalVotes: number, clases: Clase[], cupo: number ){
        this.nombre = nombre;
        this.lastname = lastname;
        this.image = image;
        this.phone = phone;
        this.password = password;
        this.email = email;
        this.description = description;
        this.type = type;
        this.username = username;
        this.rate = rate;
        this.totalVotes = totalVotes;
        this.clases = clases;
        this.cupo = cupo;
    }

}



