import { Component, OnInit } from '@angular/core';
import {User} from "../../models/user";
import { UsersService } from "../../services/users.service";
import { GroupService } from '../../services/group.service';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import {Clase} from "../../models/clase";
import { Group } from '../../models/group';

@Component({
    selector: 'app-home-signin-page',
    templateUrl: './home-signin-page.component.html',
    styleUrls: ['./home-signin-page.component.css']
})
export class HomeSigninPageComponent implements OnInit {

    user: User;
    private username:string;
    private clases: Clase[] = [];
    private flag: boolean=false;
    private flagamaru: boolean=false;
    groups:Group[]=[];

    constructor(public usersService: UsersService,  
                 public router: Router,
                 public formBuilder: FormBuilder,
                 public groupService:GroupService) { }

    isInstructor() {
        return this.flag;
    };
    isAmaru() {
        return this.flagamaru;
    }

    ngOnInit() {
        this.username=sessionStorage.getItem('username');
        this.usersService.findUserByUsername(this.username).subscribe(usersResponse4 => {
            this.user = usersResponse4;
            this.clases=usersResponse4.clases;
            if (usersResponse4.type==='INSTRUCTOR'){
                this.flag=true;
                this.flagamaru=false;
            }
            else{
                this.flag=false;
                this.flagamaru=true;
            }
        });
    }

    comprar(cantidad: number){
        this.groupService.buy(cantidad, sessionStorage.getItem('username')).subscribe(usersResponse41 => {
            this.user = usersResponse41;
            this.router.navigateByUrl('/DummyComponent', true);
            this.router.navigate(['/welcome']);
            });
    }


    onSubmit(name){
        this.groupService.getGroupByCategory(name).subscribe(categories =>{
          this.groups=categories;

        })
        
        console.log(name);
    }

    entergr(name) {
        console.log(name)
        sessionStorage.setItem('groupname',name);
        this.router.navigateByUrl('/DummyComponent', true);
        this.router.navigate(['/group']);
      }


}



