import { Injectable } from '@angular/core';
import { Group } from '../models/group';
import { User } from '../models/user';
import { Comment } from '../models/comment';
import { APIService } from '../common/api.service';
import { Observable } from "rxjs/Observable";
import { AppConfiguration } from '../common/config/app-configuration.service';
import { AuthService } from '../common/auth.service';
import { Http } from '@angular/http';
import {Clase} from "../models/clase";

@Injectable()
export class GroupService extends APIService{
    private resourceUrl = 'group/';
    group:Group;
    constructor(
        public config: AppConfiguration,
        public authService: AuthService,
        public http: Http
      ) {
        super(config, authService, http);
      }
    getGroupById(id: number): Observable<Group>{
        return this.get(this.resourceUrl + id);
    }
    create1(id: number, name: string, instructor: string, comments: Comment[], description: string, category: string, rate: number, totalVotes: number, image: string, clases: Clase[]): Observable<Group>{
        return this.post(this.resourceUrl + "groups",new Group(id, name, instructor, comments, description, category, rate, totalVotes, image, clases));
      }
    registerUserInGroup(idclase:number, idgroup:number,username:string): Observable<boolean>{
        return this.post(this.resourceUrl + "susbcribe",{idclase, idgroup, username});
    }
    editRate(id: number, rate: number){
        return this.post(this.resourceUrl + 'rate',{id,rate});
    }
    getGroupByName(search:string):Observable<Group[]>{
        return this.get(this.resourceUrl+"groupsname/"+search);
    }

        buy(cupo:number, username:string):Observable<User>{
        return this.post('user/' + 'buy/',{username, cupo});
    }

    getGroupByCategory(name:string):Observable<Group[]>{
        return this.get(this.resourceUrl+"groups/"+name);
    }

    addComment(co:Comment) {
        return this.post(this.resourceUrl + 'comment', co);
    }





}


