import { Component, OnInit } from '@angular/core';
import { User } from "../../models/user";
import { Group } from "../../models/group";
import { Comment } from "../../models/comment";
import { UsersService } from "../../services/users.service";
import { GroupService } from "../../services/group.service";
import { FormGroup, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import {Clase} from "../../models/clase";

@Component({
    selector: 'app-group-profile-page',
    templateUrl: './group-profile-page.component.html',
    styleUrls: ['./group-profile-page.component.css']
})
export class GroupProfilePageComponent implements OnInit {
    private user: User;
	private instructor: string;
    clases: Clase[]=[];
    private group: Group;
    co: Comment;
    comentarios: Comment[];
    groupForm: FormGroup;
    private result: boolean;
    private username: string;
    private groupId: number;

    constructor(public groupService :GroupService,public usersService: UsersService,   public router: Router,    public formBuilder: FormBuilder,)  {

    }

    seeprofile(){
        sessionStorage.setItem('profile',this.instructor);
        this.router.navigate(['/profile']);
    }


    isSameInstructor() {
        if (this.user.username===this.instructor){
            return false;
        }
        else{
            return true;
        }
    }

    onSubmitComment() {
        this.co = new Comment(this.groupForm.get('newComment').value, Number(sessionStorage.getItem('groupId')), this.username,  'fecha',0);
        this.groupService.addComment(this.co
        ).subscribe(groupResponse => {
            this.group = groupResponse;
            alert('Comentario Relizado Correctamente');
            this.router.navigateByUrl('/jejeje', true);
            this.router.navigate(['/group']);
        }, error=>{
            console.log(error);
        });
    }


	onSubmitRegister(idclase:number, idgroup:number) {
        this.groupService.registerUserInGroup(idclase, idgroup, String(sessionStorage.getItem("username"))).subscribe(groupResponse => {
            this.result= groupResponse;
            console.log(this.result);
            if(groupResponse){alert('Suscrito Correctamente');
                this.router.navigate(['/welcome']);
            }
            else{
                alert('No tienes el suficiente cupo, por favor suscribete a un plan');
            }
        }, error=>{
        });


    }
    onSubmitRate() {
        this.groupService.editRate(
            this.groupId,
            this.groupForm.get('newRate').value
        ).subscribe(serverResponse91=>{
            this.group = serverResponse91;
            alert('Calificaci√≥n Gurdada');
            this.router.navigateByUrl('/hola', true);
            this.router.navigate(['/group']);
        }, error=>{
            console.log(error);
        });

    };


    ngOnInit() {

        this.groupForm = this.formBuilder.group({
            newRate: '',
            newComment:''
        });
        this.username=sessionStorage.getItem('username');
		this.groupId = Number(sessionStorage.getItem('groupId'));
        this.usersService.findUserByUsername(this.username).subscribe(usersResponse90 => {
            this.user = usersResponse90;
        });
        this.groupService.getGroupById(this.groupId).subscribe(usersResponse50 => {
            this.group = usersResponse50;
            //this.clases = this.group.clases;
            this.comentarios = this.group.comments;
			this.instructor = this.group.instructor;
            for (var i = 0; i< this.group.clases.length; i++){
                console.log(this.group.clases[i].usuario+" -- "+this.instructor);
                if (this.group.clases[i].usuario === this.instructor){
                    this.clases.push(this.group.clases[i]);
                }
            }

        });
    };



}


