import { Comment } from './comment';
import { Observable } from "rxjs/Observable";
import {Clase} from './clase';

export class Group {
    id: number;
    nombre: string;
    instructor: string;
    comments: Comment[];
    description: string;
    category: string;
    rate: number;
    totalVotes: number;
    image: string;
    clases: Clase[];

    constructor(id: number, nombre: string, instructor: string,  comments: Comment[], description: string, category: string, rate: number, totalVotes: number, image: string, clases: Clase[]){
        this.id = id;
        this.nombre = nombre;
        this.instructor = instructor;
        this.comments = comments;
        this.description = description;
        this.category = category;
        this.rate = rate;
        this.totalVotes = totalVotes;
        this.image = image;
        this.clases = clases;

    }


}



