export class Clase {
    idgrupo: number ;
    fecha: string;
    hour: string;
    place: string;
    idclase: number;
    nombregrupo: string;
    numinscritos: number;
    usuario: string;



    constructor(idgrupo: number, fecha: string, hour: string, place: string, idclase: number, nombregrupo: string, numinscritos: number, usuario: string){
    this.idgrupo = idgrupo;
    this.fecha = fecha;
    this.hour = hour;
    this.place = place;
    this.idclase = idclase;
    this.nombregrupo = nombregrupo;
    this.numinscritos = numinscritos;
    this.usuario = usuario;
}

}



